.about {
  margin: 0 auto;
  padding: 60px 18px;
  background-image: url("../../images/about_bcg_thinkbig4.png");
  background-size: 110%;
  background-position: 50% 40%;
  background-repeat: no-repeat;
  position: relative;
}

@media (min-width: 768px) {
  .about {
    padding: 80px 20px 120px;
  }
}
@media (min-width: 1280px) {
  .about {
    padding: 120px 20px;
  }
}

.about__container {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  padding: 0;
}
.about__header {
  font-weight: 500;
  font-size: 34px;
  line-height: 28px;
  margin-bottom: 50px;
}
.about__content {
  margin: 20px auto 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (min-width: 768px) {
  .about__content {
    margin: 50px auto 0;
    gap: 25px;
  }
}
.about__portrait {
  opacity: 1;
  min-width: 280px;
  width: 70%;
  min-height: 280px;

  border-radius: 50%;
  align-self: center;
  transform: rotate(-90deg);
  flex-shrink: 1;
}
@media (min-width: 544px) {
  .about__portrait {
    max-width: 370px;
    max-height: 370px;
    transform: rotate(-90deg);
  }
}
@media (min-width: 768px) {
  .about__content {
    flex-direction: row;
  }
  .about__portrait {
    max-width: 350px;
  }
}

.about__text-block {
  margin: 0 auto;

  flex-shrink: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about__maintext {
  margin: 0;
  text-align: justify;
  font-size: 14px;
  line-height: 20px;
  width: 100%;
  min-width: 280px;
}
@media (min-width: 544px) {
  .about__maintext {
    font-size: 18px;
    line-height: 22px;
    min-width: 300px;
  }
}
@media (min-width: 1280px) {
  .about__maintext {
    font-size: 20px;
    line-height: 26px;
  }
}
.about__title {
  font-size: 18px;
  font-weight: 500;
}
@media (min-width: 440px) {
  .about__title {
    font-size: 20px;
  }
}

@media (min-width: 1024px) {
  .about__title {
    font-size: 22px;
  }
}

.about__decor {
  position: absolute;
  width: 200px;
  height: 30px;
  left: calc(50% - 100px);
  top: -10px;
  background-image: url("../../images/decor/Ornament22.png");
  background-size: 100%;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .about__decor {
    width: 240px;
    height: 40px;
    left: calc(50% - 120px);
    top: -10px;
  }
}
@media (min-width: 1280px) {
  .about__decor {
    width: 300px;
    height: 75px;
    left: calc(50% - 150px);
    top: -10px;
  }
}
