:root {
  --animate-delay: 0.5s;
}

body {
  max-width: 100%;
  background: #f5f5f5;
  /* font-family: "Inter", sans-serif; */
  /* font-family: "Poppins", sans-serif; */
  font-family: "Roboto", sans-serif;
  color: #232323;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}
#root {
  width: 100%;
}
.page {
  margin: 0 auto;
}

.back-top {
  display: block;
  width: auto;
  float: right;
  height: 24px;
  position: fixed;
  bottom: 18px;
  z-index: 999;
  transition: opacity 0.4s ease-in-out, visibility 0.4s;
  border-radius: 50%;
  border: 2px solid #ff880000;
  box-sizing: content-box;
}
._position1 {
  right: 10px;
}
._position2 {
  right: 42px;
}
.ui-link {
  cursor: pointer;
  display: inline-block;
  height: 24px;
  width: 24px;
  background: #ff8800;
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border-radius: 50%;
}

.ui-link:focus {
  outline-style: none;
  outline: none;
}

@media (min-width: 768px) {
  .back-top {
    bottom: 13px;
    height: 32px;
  }
  .ui-link {
    height: 32px;
    width: 32px;
  }
  ._position1 {
    right: 10px;
  }
  ._position2 {
    right: 52px;
  }
}
.back-top:hover {
  outline: 1px solid #ff8800;
}
.go-top {
  background-image: url("../../images/back_top.png");
}

.download-cv {
  background-image: url("../../images/icons_cv.png");
}

/* ::-webkit-scrollbar {
  display: none;
} */

._accent {
  color: #8822aa;
}
._section__header {
  font-weight: 500;
  font-size: 34px;
  line-height: 32px;
  margin: 0;
  text-align: center;
  opacity: 1;
  z-index: 7;
}
._invert {
  color: #f5f5f5;
}
._huge {
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 420px) {
  ._huge {
    font-size: 48px;
    line-height: 48px;
  }
}
@media (min-width: 768px) {
  ._huge {
    font-size: 56px;
    line-height: 56px;
  }
}
@media (min-width: 1024px) {
  ._huge {
    font-size: 62px;
    line-height: 62px;
  }
}

._container-narrow {
  max-width: 984px;
}
@media (min-width: 768px) {
  ._container-narrow {
    max-width: 984px;
    margin: 0 auto;
  }
}

._container-wide {
  max-width: 1184px;
}
@media (min-width: 768px) {
  ._container-wide {
    max-width: 984px;
    margin: 0 calc(24 * (100vw - 768px) / 256);
  }
}
@media (min-width: 1024px) {
  ._container-wide {
    margin: 0 24px;
  }
}
@media (min-width: 1079px) {
  ._container-wide {
    margin: 0 auto;
  }
}
._container-xtra {
  max-width: 1392px;
}
@media (min-width: 768px) {
  ._container-xtra {
    /* margin: 0 calc(24 * (100vw - 768px) / 672); */
  }
}
@media (min-width: 1440px) {
  ._container-xtra {
    margin: 0 auto;
  }
}

._hide {
  opacity: 0;
  visibility: hidden;
}
